<template>
  <div>
    <p>同一组件内，自定义事件</p>
    <p>$emit 触发事件，传递参数  this.$on('customEvent', function(arg){})</p>
    <p>$on 监听事件，获取到参数  this.$emit('customEvent',123)</p>
    <button v-on:click="eventHandler">push</button>
    <p></p>
    <p>$off 取消监听事件  this.$off('customEvent')</p>
    <button v-on:click="detachEvent">取消监听事件</button>
    <p>说明： 这是用在同一个组件实例内，不是父子组件间传值</p>
  </div>
</template>
<script>
export default {
  name: "customevent",
  mounted(){
    this.$on('customEvent', function(arg){
        console.log(arg)
    })
  },
  methods: {
    eventHandler(){
      this.$emit('customEvent',123)
    },
    detachEvent(){
      this.$off('customEvent')
    }
  }
}
</script>