<template>
  <div :id="chartId" :style="style" :lineData="lineData"></div>
</template>
<script>
// 将 echarts 封装成组件，props 接收参数，data是父组件分发给 echarts 的数据源
export default {
  props: {
    width: {
      type: String,
      default: "450px"
    },
    height: {
      type: String,
      default: "300px"
    },
    lineData: {
      type: Object
    }
  },
  data() {
    return {
      myChart: null
    };
  },
  computed: {
    chartId: function() {
      return "id-" + Math.floor(Math.random()*100000);
    },
    style() {
      return {
        width: this.width,
        height: this.height
      };
    }
  },
  mounted() {
    this.renderLine();
    this.resizeChart();
  },
  beforeDestroy() {
    window.onresize = null;
  },
  methods: {
    resizeChart() {
      window.onresize = () => {
        this.myChart.resize();
      };
    },
    renderLine() {
      let lineData = this.lineData;
      let opts = lineData;

      this.myChart = this.$echarts.init(document.getElementById(this.chartId));
      this.myChart.setOption(opts);
    }
  },
  watch: {
    // 深度监听传入的数据变化
    lineData: {
      handler(newVal, val) {
        this.data = newVal;
        this.myChart.dispose();
        this.renderLine();
      },
      deep: true
    }
  }
};
</script>
